import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Navbar, NavDropdown } from "react-bootstrap";
import "./Navbar.css";

export default function Nav_bar() {
  return (
    <div>
      <Navbar className="navbar" variant="k" expand="lg">
        <div className="cont">
          <div>
            <Navbar.Brand
              className="title"
              style={{ marginLeft: "20px", color: "white" }}
              href="#home"
            >
              Pathfinding Visualizer
            </Navbar.Brand>
          </div>
          <div className="buttons">
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <div className="list button" href="#home">
                clear
              </div>
              <div className="disabled" target="_blank">
                Run!
              </div>
              <div style={{ display: "flex", flexDirection: "row" }}>
                <NavDropdown title={"Algorithms"} id="nav-dropdown">
                  <NavDropdown.Item disabled="true" href="#action/3.4">
                    Select an Algorithm
                  </NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item href="#action/3.1">
                    Dijkstra
                  </NavDropdown.Item>
                  <NavDropdown.Item href="#action/3.2">
                    Breath First Search
                  </NavDropdown.Item>
                  <NavDropdown.Item href="#action/3.3">
                    Depth First Search
                  </NavDropdown.Item>
                </NavDropdown>
              </div>
            </div>
          </div>
        </div>
      </Navbar>
    </div>
  );
}
